import React, { Component } from 'react';
import { Tooltip } from '../src';
import TWDate from '../src/date';

export default class DateDocs extends Component {
  constructor(props) {
    super(props);
    this.state = {
      label: 'Wow',
      customPosition: false,
      position: Tooltip.Position.TOP,
      customOffset: false,
      offset: 0,
    };
  }

  createStateLink(name) {
    return value => this.setState({ [name]: value });
  }

  render() {
    return (
      <div className="container">
        <section className="section">
          <div className="row">
            <div className="col-md-6">
              <h2>Date</h2>
              <p>One of the most romantic component...</p>
            </div>
            <div className="col-md-6">
              <TWDate onChange={() => {}} locale="en-GB" />
            </div>
          </div>
          <div className="row">
            <div className="col-md-6">
              {/* eslint-disable react/jsx-indent */}
              <pre className="tw-docs-code">
                {`<Tooltip
  label={"${this.state.label}"}
  position={${
    this.state.customPosition && this.state.position
      ? `Tooltip.Position.${this.state.position.toUpperCase()}`
      : undefined
  }}
  offset={${this.state.customOffset ? this.state.offset : undefined}}
>
  <button className="btn btn-default">
    Hover me
  </button>
</Tooltip>`}
              </pre>
              {/* eslint-enable react/jsx-indent */}
              <p>Every property that is undefined can be omitted.</p>
            </div>
          </div>
        </section>
      </div>
    );
  }
}



