import { formatDate } from '@transferwise/formatting';
import { MONTHS_EN } from '../../data';

export const getMonthNames = (locale, format = 'long') => {
  const months = [];
  const date = new Date(2018, 0, 1);
  if (isIntlSupportedForLocale(locale)) {
    while (months.length < 12) {
      months.push(formatDate(date, locale, { month: format }));
      date.setMonth(date.getMonth() + 1);
    }
    return months;
  }
  return MONTHS_EN;
};

export const isIntlSupportedForLocale = locale =>
  window &&
  window.Intl &&
  typeof window.Intl === 'object' &&
  window.Intl.DateTimeFormat.supportedLocalesOf([locale]).length > 0;



